body{font-size:16px;background-color:#f4f5f7}#heading-container{height:10vh;width:95vw;padding-top:1vh;padding-left:5vh;padding-right:5vh;display:flex;-moz-flex-direction:row nowrap;flex-direction:row nowrap;-moz-justify-content:space-between;-ms-justify-content:space-between;justify-content:space-between;-ms-flex-pack:space-between;-moz-align-items:center;-ms-align-items:center;align-items:center}#heading-container span{font-family:"Trochut";color:#FAB8A1;font-size:2.5em}#heading-container span:hover{color:#f79371;cursor:pointer}#heading-container div#user-modal{height:5vh;width:5vh;background-color:#7DAC9A;border-radius:30px}#heading-container div#user-modal.user-modal-active,#heading-container div#user-modal:hover{background-color:#609681;cursor:pointer}#heading-container div#user-modal div{position:fixed;width:9vw;height:auto;right:3.75vh;margin-top:6vh;background-color:#fff;border-radius:10px;box-shadow:0px 1px 5px 0px rgba(0,0,0,0.25)}#heading-container div#user-modal div p{color:#6C6B85;margin-left:1vw}#heading-container div#user-modal div p:hover{font-weight:bold;cursor:pointer}

