body{font-size:16px;background-color:#f4f5f7}div#new-little-container{position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);z-index:2;max-width:100%;max-height:100%;height:55vh;width:50vw;padding:1%;box-shadow:0px 5px 10px 0px rgba(0,0,0,0.25);background-color:#fff;border-radius:30px}div#new-little-container img{position:absolute;height:20px;width:20px;top:30px;right:35px;transition:all .1s ease-in-out;cursor:pointer;-webkit-filter:saturate(1) brightness(1.75);filter:saturate(1) brightness(1.75)}div#new-little-container img:hover{transform:scale(1.15);transition:all .1s ease-in-out}div#new-little-container h2{font-family:"Yeseva One";font-size:2em;color:#6C6B85;text-align:center;margin-top:5%}div#new-little-container div{height:90%;width:100%;display:flex;flex-flow:column nowrap}div#new-little-container div textarea[type='text']{resize:none;height:100%;width:90%;margin-left:5%;margin-top:2.5%;font-size:4em;color:#6C6B85;text-align:center;border:none}div#new-little-container div textarea[type='text']::-webkit-input-placeholder{color:#bfbecb}div#new-little-container div textarea[type='text']:-ms-input-placeholder{color:#bfbecb}div#new-little-container div textarea[type='text']::-ms-input-placeholder{color:#bfbecb}div#new-little-container div textarea[type='text']::placeholder{color:#bfbecb}div#new-little-container div div{display:flex;flex-flow:row nowrap;-moz-justify-content:space-around;-ms-justify-content:space-around;justify-content:space-around;-ms-flex-pack:space-around;-moz-align-items:center;-ms-align-items:center;align-items:center}div#new-little-container div div button.save-little-btn{width:40%;height:25%;color:#fff;border-radius:10px;border:none;font-family:"Open Sans";font-size:1.5em}div#new-little-container div div button.save-little-btn#nerve{background-color:#FAB8A1;transition:all .2s ease}div#new-little-container div div button.save-little-btn#nerve:hover{color:#f9b197;font-weight:bold;background-color:white;border:3px solid #FAB8A1;transiti