body{font-size:16px;background-color:#f4f5f7}div#littles-list-container{height:85vh;width:35vw;margin-right:5vh;padding:1vh 0 1vh 0;border-radius:30px;background-color:#fff;box-shadow:0px 5px 8px 5px rgba(0,0,0,0.25)}div#littles-list-container div#littles-list{height:94%;width:100%;margin-top:2.5vh;padding-bottom:1vh;display:flex;flex-flow:column nowrap;-moz-align-items:center;-ms-align-items:center;align-items:center;overflow:scroll;overflow-x:hidden}div#littles-list-container div#littles-list::-webkit-scrollbar{width:5px}div#littles-list-container div#littles-list::-webkit-scrollbar-thumb{background-color:rgba(0,0,0,0.15);border-radius:50px}

